import React from'react'
import PropTypes from'prop-types'
const scrollMark = ({color}) => {
    let propColor = color === 'white' ? true : false;
    console.log(propColor)
    return (
        <div className="mouse" style={{background: propColor ? 'white' : 'black'}}>
            <div className="line"style={{background: propColor ? 'black' : 'white'}}></div>
        </div>
    )
}

scrollMark.defaultProps = {
    color: 'white'
}

scrollMark.propTypes = {
    color: PropTypes.any.isRequired
}


export default scrollMark
