import React from "react"
import { graphql } from "gatsby"
import '../styles/main.scss'
import Icon from'../assets/menu-button.svg'
import { List, ScrollMark, WorkList } from "../components"

const IndexPage = ({data: {allMarkdownRemark : {edges}}}) => {
  return(
    <>
      <div className="container">
        <section id="welcome">
          <header>
            <div className="page">01. Welcome</div>
            <Icon/>
          </header>
          <h1 className="titleText">Hello!, I'm Caner Tuzunar, Curious Software Developer</h1>
          <ScrollMark color="black"/>
        </section>
        <section id="aboutMe" className="black">
          <header>
            <div className="page">02. About Me</div>
            <Icon className="svgBlack"/>
          </header>
          <p className="description">
            I studied web design and programming in 
            high school and developed web sites and applications.
          </p>
          <div className="attr">
            <h3>Software / Tools</h3>
            <ul className="attr-item">
              <li>Figma</li>
              <li>VS Code</li>
              <li>Terminal</li>
              <li>Codepen</li>
              <li>Github</li>
              <li>Jenkins</li>
            </ul>
          </div>
          <div className="attr">
            <h3>Development</h3>
            <ul className="attr-item">
              <li>HTML</li>
              <li>CSS</li>
              <li>Javascript</li>
              <li>TypeScript</li>
              <li>React JS</li>
              <li>Node JS</li>
              <li>GraphQL</li>
              <li>MongoDB</li>
              <li>Yarn</li>
              <li>NPM</li>
              <li>Git</li>
              <li>Kubernetes</li>
              <li>Lambda</li>
              <li>Docker</li>
              <li>ECS</li>
              <li>EC2</li>
              <li>Serverless</li>
            </ul>
          </div>
          <ScrollMark color="white"/>
        </section>
        <section id="works">
          <header>
            <div className="page">03. Works</div>
            <Icon/>
          </header>
          <h3 className="sectionTitle">Crafted With Love.</h3>
          <main><WorkList/></main>
          <ScrollMark color="black"/>
        </section>
        <section id="welcome" className="black">
          <header>
            <div className="page">04. Blog</div>
            <Icon className="svgBlack"/>
          </header>
          <main>
            <List data={edges}/>
          </main>
          <ScrollMark color="white"/>
        </section>
      </div>
    </>
  )
}

export default IndexPage
export const query = graphql`
  {
    allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/posts/"}}, sort: {fields: frontmatter___date, order: DESC}) {
        edges {
          node {
            id
            fields {
              slug
            }
            frontmatter{
              title
              date
            }
        }
      }
    }
  }
`